$BACKGROUND_COLOR: #2680D9;
$BUTTON_ACTIVE_COLOR: #1A69B8;
$COUNTER_COLOR: #F53648;
$FONT_FAMILY: 'Roboto', 'Segoe UI', Tahoma, Verdana, sans-serif;
.Menu{
    background: $BACKGROUND_COLOR;
    display: flex;
    flex-direction: column;
    width: 80px;
    height: 830px;
    align-items: center;
    justify-content: space-between;
    transition: width 0.2s ease;
    &_expanded{
        width: 247px;
    }
}
.Menu-Shell{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Menu-Avatar{
    width: 50px;
    height: 50px;
    border-radius: 40px;
    border: 2px solid white;
    margin: 10px 15px;

    &_expanded{
        width: 74px;
        height: 74px;
    }
}
.Menu-Name{
    font-family: $FONT_FAMILY;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.1px;
    color: #FFF;
    text-align: center;
}
.Menu-Status{
    font-family: $FONT_FAMILY;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.4px;
    color: #FFF;
    text-align: center;
    margin-bottom: 36px;
}
.Menu-StatusBar{
    display: inline-block;
    border-radius: 50%;
    width: 8px;
    height: 8px;
    margin-right: 8px;
    background-color: #01BF93;;
}
.Menu-Wrapper{
    width: 100%;
    height: 72px;
    background: $BACKGROUND_COLOR;
    padding: 0;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    
    &_expanded{
        padding-left: 28px;
        justify-content: start;
    }
}
.Menu-MessagesWrapper{
    &:hover{
        opacity: 0.7;
    }
    &_active{
        background-color: $BUTTON_ACTIVE_COLOR;
    }
    transition: opacity 0.3s ease;
}
.Menu-SettingsWrapper{
    &:hover{
        opacity: 0.7;
    }
    &_active{
        background-color: $BUTTON_ACTIVE_COLOR;
    }
    transition: opacity 0.3s ease;
}
.Menu-Text{
    color: #FFF;
    font-size: 14px;
    font-family: $FONT_FAMILY;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.1px;
    text-transform: uppercase;
    margin-left: 20px;
    margin-right: 60px;
}
.Menu-Messages{
    background-image: url('./../../images/icon-message.svg');
    width: 24px;
    height: 24px;
    position: relative;
}
.Menu-MessagesCounter{
    width: 14px;
    height: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 11px;
    background: $COUNTER_COLOR;
    box-shadow: 0px 3px 7px 0px rgba(17, 68, 117, 0.35);
    font-size: 8px;
    font-family: $FONT_FAMILY;
    line-height: 250%; /* 250% */
    letter-spacing: 0.4px;
    color: #FFF;
    position: absolute;
    bottom: -5px;
    right: -7px;

    &_expanded{
        position: static;
        width: 23px;
        height: 23px;
        line-height: 166.667%;
        font-size: 12px;
        margin-right: 22px;
    }
}
.Menu-Settings{
    background-image: url('./../../images/icon-settings.svg');
    width: 24px;
    height: 24px;
}
.Menu-ButtonOpenWrapper{
    border-radius: 4px;
    background: #58AAFC;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 3px 8px;
    margin-bottom: 20px;
    border: none;

    &_expanded{
        width: 100%;
        max-width: 207px;
        padding: 3px 0px;
    }
}
.Menu-ButtonOpen{
    background-image: url('./../../images/icon-menu.svg');
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;

    &_expanded{
        transform: rotate(180deg);
    }
}